<!--
 * @Author: linxu
 * @Date: 2021-11-11 16:49:53
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-02 11:30:57
 * @Description: 机组信息页面
-->
<template>
  <div>
    <BMainTopComponent />
    <div id="crewinfo">
      <div class="tab-box">
        <el-button
          v-for="item in tabs"
          :key="item.status"
          :class="['status-tab', { 'select-tab': searchForm.crewStatus == item.status }]"
          @click="changeStatus(item.status)"
          >{{ item.label }}({{ item.value }})</el-button
        >
      </div>
      <div class="search-box">
        <div class="search-item">
          <span>机组编码搜索:</span>
          <el-input
            placeholder="请输入机组编码"
            suffix-icon="el-icon-date"
            v-model="searchForm.crewCode"
          >
          </el-input>
        </div>
        <div class="search-item">
          <span>机组位置筛选:</span>
          <position-cascader
            placeholder="请选择机组所在位置"
            :defaultPosition="searchForm.crewPosition"
            @positionChange="positionChange"
          />
          <!-- <el-cascader
            v-model="selectedOptions"
            placeholder="请选择机组所在位置"
            :options="options"
            @change="positionChange"></el-cascader> -->
        </div>
        <el-button type="primary" @click="resetCurrentParams()">查 询</el-button>
      </div>
      <TableList
        :options="options"
        :columns="columns"
        :dataSource="dataSource"
        :dataTotal="dataTotal"
        :pagination="paginationCfg"
        @handleChangePage="handleChangePage"
        @handleChangeSize="handleChangeSize"
        border
        v-loading="loading"
      >
        <el-table-column
          slot="operation"
          label="操作"
          align="center"
          v-bind="options && options.props"
        >
          <template slot-scope="scope">
            <div class="operate-group">
              <el-button type="text" @click="showDetail(scope.row, 0)"> 机组信息 </el-button>
              <el-button type="text" @click="showDetail(scope.row, 1)"> 机组报告 </el-button>
            </div>
          </template>
        </el-table-column>
      </TableList>
    </div>
  </div>
</template>

<script>
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import { mapActions } from 'vuex'
import PositionCascader from '@/components/PositionCascader'

export default {
  name: 'crewinfo',
  mixins: [paginationConfigMixin],
  components: {
    PositionCascader
  },
  data() {
    return {
      crewStatusList: ['离线', '停机', '警告', '报警', '运行'],
      tabs: [
        { type: 'GensetAll', status: 0, label: '全部机组', value: 0 },
        { type: 'GensetRun', status: 4, label: '运行', value: 0 },
        { type: 'GensetAlarm', status: 2, label: '报警', value: 0 },
        { type: 'GensetWarn', status: 3, label: '警告', value: 0 },
        { type: 'GensetStop', status: 5, label: '停机', value: 0 },
        { type: 'GensetOffline', status: 6, label: '离线', value: 0 }
      ], // 0 所有 2报警 3警告 4运行 5 停机 6离线
      searchForm: {
        crewStatus: 0, // 默认选中状态
        crewCode: '',
        crewPosition: ''
      },
      loading: false,

      // 表格
      columns: [
        {
          label: '机组名称',
          prop: 'DisplayName',
          minWidth: '12%'
        },
        {
          label: '机组状态',
          prop: 'StatusText',
          minWidth: '5%'
        },
        {
          label: '安装地点',
          prop: 'InstallationSite',
          minWidth: '12%'
        },
        {
          label: '经纬度',
          prop: 'LongitudeAndLatitude',
          minWidth: '12%'
        }
      ],
      dataSource: [],
      options: {
        // height: '100%',
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 160 // 操作列宽度
        }
      }
    }
  },
  mounted() {
    this.getCrewStatusCount()
    this.queryTableData()
  },
  methods: {
    ...mapActions(['toggleSelectCrew']),
    getCrewStatusCount() {
      const params = {
        DisplayMode: 1, // 1为所有启用，0为所有机组 2为所有禁用
        IsLease: 1, // 租赁
        UserID: -1
      }
      this.$api.equipment.findGenSetStatusCount(params).then((res) => {
        // console.log(res)
        const data = res.Data
        if (res.IsSuccess) {
          this.tabs.forEach((item) => {
            item.value = data[item.type]
          })
        }
      })
    },
    queryTableData() {
      const query = {
        // IsWeb: true,
        Page: this.paginationCfg.currentPage,
        PageSize: this.paginationCfg.pageSize
      }
      const params = {
        model: {
          DisplayMode: 1, // 1为所有启用，0为所有机组 2为所有禁用
          IsLease: 1, // 租赁
          UserID: -1,
          StatusMode: this.searchForm.crewStatus, // 0 所有 2报警 3警告 4运行 5 停机 6离线
          CollectorToken: this.searchForm.crewCode, // 标识
          InstallationSite: this.searchForm.crewPosition
        }
      }
      this.loading = true
      this.$api.common
        .getCrewList(params, query)
        .then((res) => {
          if (res.IsSuccess) {
            const data = res.Data
            this.dataSource = data.Data.map((item) => {
              item.LongitudeAndLatitude = item.Longitude + ',' + item.Latitude
              item.StatusText = this.crewStatusList[item.Status]
              return item
            })
            this.dataTotal = data.TotalItemCount
            this.loading = false
          }
        })
        .catch(() => {
          this.loading = false
        })
    },

    // 切换tab
    changeStatus(val) {
      this.searchForm.crewStatus = val
      this.resetCurrentParams()
      // this.queryTableData()
    },
    // 操作按钮 跳转路由页面
    showDetail(row, type) {
      const path = type === 0 ? '/equipment/crewinfo-detail' : '/equipment/crewreport-detail'
      this.toggleSelectCrew(row)
      // this.$router.push(path) // {path, query: {crewId: row.ID}}
      window.open(`/#${path}`)
    },
    positionChange(data) {
      // console.log(data)
      this.searchForm.crewPosition = data.crewPosition
    }
  }
}
</script>

<style lang="less" scoped>
#crewinfo {
  display: initial !important;
  .tab-box {
    margin-bottom: 16px;
    .select-tab {
      background-color: #1abc9c !important;
      color: #fff !important;
      border: 0;
    }
  }
  .el-table__cell:first-child .cell {
    white-space: nowrap;
  }
}
</style>



